<template>
  <div class="container">
    <div class="row robotContainer">
      <div class="col-md-6 textLeftOfRobot text-white">
        <h1>Hack<sup>2</sup></h1>
        <h3>
          A hackathon for the people, by the people. With countless
          possibilities and issues to tackle, the sky's the limit.
        </h3>
      </div>
      <div v-if="ready" class="col-md-6 px-0 robot" ref="canvasContainer">
        <canvas ref="canvas"></canvas>
      </div>
      <div class="col-md-12 text-center d-none d-md-block">
        <ChevronDownIcon
          @click="goDown"
          class="jump"
          size="2.4x"
        ></ChevronDownIcon>
      </div>
    </div>
  </div>
</template>
<style scoped>
@keyframes jump {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
}
.jump {
  cursor: pointer;
  animation: 2s jump infinite ease-in-out;
}
.robot {
  height: 60vh;
}
.robotContainer {
  min-height: 90vh;
  /* max-width: 100% !important; */
  /* background: rgba(0, 0, 0, 0.99); */
}
.textLeftOfRobot {
  padding-top: 10vw;
  /* padding-left: 5vw !important; */
}
</style>
<script>
import { ChevronDownIcon } from "vue-feather-icons";
import {
  WebGLRenderer,
  Scene,
  PerspectiveCamera,
  // Clock,
  Box3,
  // PointLight,
  HemisphereLight,
  SpotLight,
  // AmbientLight,
  // sRGBEncoding,
  // LoopPingPong,
} from "three";
const sRGBEncoding = 3001;
// import { WebGLRenderer } from "three/src/renderers/WebGLRenderer";
// import { Scene } from "three/src/scenes/Scene";
// import { PerspectiveCamera } from "three/src/cameras/PerspectiveCamera";
// import { Box3 } from "three/src/geometries/BoxGeometry";
// import { HemisphereLight } from "three/src/lights/HemisphereLight";
// import { SpotLight } from "three/src/lights/SpotLight";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

export default {
  data() {
    return {
      mouseX: 0.5,
      mouseY: 0.5,
      mixer: null,
      robot: null,
      // clock: null,
      scene: null,
      camera: null,
      renderer: null,
      ready: false,
      ticker: 0,
      robotGLB: require("../../assets/models/cube.glb"),
    };
  },
  components: {
    ChevronDownIcon,
  },
  methods: {
    goDown() {
      window.scrollTo({
        left: 0,
        top:
          document.querySelector(".bg-full-hack").offsetTop -
          document.querySelector(".nav").offsetHeight,
        behavior: "smooth",
      });
    },
    resize() {
      this.dispose();
      this.init();
    },
    setSize() {
      this.renderer.setSize(
        this.$refs.canvasContainer.offsetWidth,
        this.$refs.canvasContainer.offsetHeight
      );

      // this.camera = new PerspectiveCamera(
      //   40,
      //   this.$refs.canvasContainer.clientWidth /
      //     this.$refs.canvasContainer.clientHeight,
      //   0.1,
      //   1000
      // );
      // this.fitCameraToObject(
      //   this.camera,
      //   this.object,
      //   window.innerWidth / window.innerHeight
      // );
    },
    updateMouse(e) {
      this.mouseX = e.clientX / window.innerWidth;
      this.mouseY = e.clientY / window.innerHeight;
      // console.log(this.mouseX, this.mouseY);
    },
    fitCameraToObject(camera, object, offset = 1.75, controls) {
      const boundingBox = new Box3();
      boundingBox.setFromObject(object);

      const center = boundingBox.getCenter();

      center.y -= 20;

      const size = boundingBox.getSize();

      const maxDim = Math.max(size.x, size.y, size.z);
      const fov = camera.fov * (Math.PI / 180);
      let cameraZ = Math.abs((maxDim / 4) * Math.tan(fov * 2));

      cameraZ *= offset;

      camera.position.z = cameraZ;

      const minZ = boundingBox.min.z;
      const cameraToFarEdge = minZ < 0 ? -minZ + cameraZ : cameraZ - minZ;

      camera.far = cameraToFarEdge * 3;
      camera.updateProjectionMatrix();
      camera.lookAt(center);
      if (controls) {
        controls.target = center;
        controls.target.y += 20;
        // controls.maxDistance = cameraToFarEdge * 2;
        controls.saveState();
        controls.update();
      } else {
        camera.lookAt(center);
      }
    },
    init() {
      // if (window.innerWidth <= 767) return;
      console.time("Render");
      // this.clock = new Clock();
      this.scene = new Scene();
      this.ready = true;
      this.$nextTick(async () => {
        this.camera = new PerspectiveCamera(
          40,
          this.$refs.canvasContainer.clientWidth /
            this.$refs.canvasContainer.clientHeight,
          0.1,
          1000
        );
        this.renderer = new WebGLRenderer({
          alpha: true,
          canvas: this.$refs.canvas,
          antialias: true,
        });
        console.timeLog("Render", "init");
        this.setupRenderer();
        console.timeLog("Render", "renderer");
        this.setupControls();
        console.timeLog("Render", "Controls");
        this.setupLights();
        console.timeLog("Render", "lights");
        this.addRobot();
        console.timeLog("Render", "robot");
        this.setSize();
        console.timeLog("Render", "size");
        console.timeEnd("Render");
        const id = Math.random();
        this.ticker = id;
        this.animate(id);
      });
    },
    setupControls() {
      this.controls = new OrbitControls(this.camera, this.$refs.canvas);
      this.controls.autoRotate = true;
      this.controls.enableZoom = false;
      this.controls.enablePan = false;
      this.controls.enableDamping = true;
      this.controls.dampingFactor = 0.1;
      // this.controls.addEventListener("end", () => {
      //   // this.controls.target.x = 0;
      // });
    },
    animate(id) {
      if (!this.renderer) return;
      if (this.ticker == id) requestAnimationFrame(() => this.animate(id));
      // var delta = this.clock.getDelta();
      this.controls.update();

      // this.light.position.copy(this.controls.position);
      // controls.update();
      // var radius = 20;
      // scene.position.y = (this.mouseX - 0.5) * 2;
      // scene.position.x = (this.mouseY - 0.5) * 2;
      // if (this.robot.rotation.y >= 2) direction = -1;
      // else if (robot.rotation.y <= 0.5) direction = 1;
      // console.log(direction);
      // if (direction - 1) {
      //   robot.rotation.y +=
      //     (direction - robot.rotation.y / 2) * 0.01 + direction * 0.005;
      // } else {
      //   robot.rotation.y +=
      //     (direction + robot.rotation.y / 2) * 0.01 - direction * 0.005;
      // }
      // if (robot.rotation.y == -1) {
      //   robot.rotation.y -= (1 - robot.rotation.y / 2) * 0.01;
      //   // camera.rotation.z += delta;
      // } else {
      //   robot.rotation.y += (1 - robot.rotation.y / 2) * 0.01;
      // }
      // console.log(this.controls.touches);
      // this.mixer.update(delta);
      this.renderer.render(this.scene, this.camera);
    },
    addRobot() {
      return new Promise((res) => {
        const loader = new GLTFLoader();
        loader.load(this.robotGLB, (gltf) => {
          // this.mixer = new AnimationMixer(gltf.scene);
          // const action = this.mixer.clipAction(gltf.animations[0]);
          // action.play();
          this.robot = gltf.scene;
          this.robot.rotation.y = 1;
          // this.robot.traverse((n) => {
          //   if (n.isMesh) {
          //     // n.castShadow = true;
          //     // n.receiveShadow = true;
          //     if (n.material.map) {
          //       n.material.map.anisotropy = 16;
          //       n.material.map.encoding = sRGBEncoding;
          //     }
          //   }
          // });
          this.scene.add(this.robot);
          this.fitCameraToObject(
            this.camera,
            this.robot,
            undefined,
            this.controls
          );
          res();
          // controls.update();
        });
      });
    },
    setupLights() {
      // this.light = new DirectionalLight(0xffffff, 1);
      // this.light.position.copy(this.controls.position);
      // this.scene.add(this.light);

      const hemiLight = new HemisphereLight(0xffffff, 0xffffff, 0.5);
      this.scene.add(hemiLight);

      const spotlight = new SpotLight(0xffffff, 1);
      spotlight.position.set(-50, 50, 50);
      // spotlight.castShadow = true;
      // spotlight.position.set(
      //   this.camera.position.x + 10,
      //   this.camera.position.y + 10,
      //   this.camera.position.z + 10
      // );
      this.scene.add(spotlight);
      const spotlight2 = new SpotLight(0xffffff, 1);
      spotlight2.position.set(50, -50, -50);
      // spotlight.castShadow = true;
      // spotlight2.position.set(
      //   this.camera.position.x + 10,
      //   this.camera.position.y + 10,
      //   this.camera.position.z + 10
      // );
      this.scene.add(spotlight2);
    },
    setupRenderer() {
      this.renderer.outputEncoding = sRGBEncoding;
      this.renderer.shadowMap.enabled = true;
    },
    dispose() {
      if (this.renderer) this.renderer.dispose();
      this.renderer = null;
      this.ready = false;
    },
  },
  mounted() {
    this.init();
    window.onresize = this.resize;

    // document.addEventListener("mousemove", this.updateMouse);
  },
  destroy() {
    this.dispose();
    // document.removeEventListener("mousemove", this.updateMouse);
    document.removeEventListener("resize", () => this.resize());
  },
  // mounted() {

  // },
};
</script>
<style>
h1 {
  font-size: 4rem !important;
}
</style>
