<template>
  <div class="stats">
    <div class="container py-5">
      <div class="row">
        <div class="col-md-6">
          <h1>Our Stats</h1>
          <hr />
          <p>
            Hack For The People is an intercollegiate hackathon designed to
            engage students in tackling some of the worlds largest problems.
            From issues in the economy to trying to channel people's inner
            patriotism, the sky's the limit.
          </p>
          <div class="container px-0 mx-0">
            <div class="row px-0 mx-0">
              <div
                class="col-12 p-0 mt-4 stat"
                v-for="stat in stats"
                :key="stat.text"
              >
                <div class="container">
                  <div class="row text-center text-md-left">
                    <div class="col-md-2 p-md-0">
                      <UsersIcon
                        size="3x"
                        v-if="stat.icon == 'users'"
                      ></UsersIcon>
                      <MapIcon size="3x" v-if="stat.icon == 'map'"></MapIcon>
                      <ToolIcon size="3x" v-if="stat.icon == 'tool'"></ToolIcon>
                    </div>
                    <div class="col-md-10">
                      <h2>
                        {{ stat.text }} <b>{{ stat.value }}</b>
                        {{ stat.units }}
                      </h2>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-md-6 mt-5 mt-md-0">
          <b-img-lazy
            :src="require('@/assets/images/stats.svg')"
            class="img-fluid"
          ></b-img-lazy>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { UsersIcon, MapIcon, ToolIcon } from "vue-feather-icons";

export default {
  components: {
    UsersIcon,
    MapIcon,
    ToolIcon,
  },
  methods: {
    animateStat(idx, timeout, mult) {
      return new Promise((res) => {
        const stat = this.stats[idx];
        this.stats[idx].value += stat.step * mult;
        this.stats[idx].value = Math.round(stat.value);
        if (mult < 0) {
          if (this.stats[idx].value <= 0) {
            res();
            return (this.stats[idx].value = 0);
          }
        } else {
          if (this.stats[idx].value >= stat.number) {
            return res();
          }
        }
        setTimeout(async () => {
          await this.animateStat(idx, timeout, mult);
          res();
        }, timeout);
      });
    },
    async animateText(multiplier) {
      this.animating = true;
      let promises = [];
      for (let i in this.stats) {
        promises.push(
          this.animateStat(
            i,
            (this.stats[i].step / this.stats[i].number) * 1000,
            multiplier
          )
        );
      }
      await Promise.all(promises);
      this.animating = false;
    },
  },
  watch: {
    scrollValue() {
      const scrolled = this.scrollValue;
      if (scrolled > window.innerHeight / 2) {
        if (this.animating) return;
        if (this.stats.filter((x) => x.value != x.number).length == 0) return;
        this.animateText(1);
      } else if (this.stats[0].value > 0 && scrolled < window.innerHeight / 4) {
        this.stats.forEach((x, i) => {
          this.stats[i].value = 0;
        });
      }
    },
  },
  props: ["scrollValue"],
  data() {
    return {
      animating: false,
      stats: [
        {
          step: 100,
          value: 0,
          number: 3500,
          icon: "users",
          units: "people",
          text: "With",
        },
        {
          step: 1,
          value: 0,
          number: 27,
          icon: "map",
          units: "countries",
          text: "In",
        },
        {
          step: 1,
          value: 0,
          number: 184,
          icon: "tool",
          units: "projects",
          text: "Creating",
        },
      ],
    };
  },
};
</script>

<style>
.stats {
  color: black;
  background: white;
  padding: 40px 10px;
}
.stat h2 {
  font-size: 40px;
}
.stat svg {
  color: var(--hftp-blue);
}
</style>
