      scrollValue: window.pageYOffset,
    };
  },
  methods: {
    scrollUpdate() {
      this.scrollValue = window.pageYOffset;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollUpdate, { passive: true });
